/* eslint-disable no-param-reassign */
import originalAxios from 'axios';
import envs from '@/utils/envs';
import token from '@/utils/token';

export const axios = originalAxios.create({
  baseURL: `//${envs.VUE_APP_API_DOMAIN}/api/dsz-api/`,
});

// export function pipeAxios(data) {
//   if (data.code !== 200) {
//     data = new Error(data);
//   }
//   if (data instanceof Error) {
//     return Promise.reject(new Error(data));
//   }
//   return data;
// }
export const defaultError = { code: 500, message: 'System Error' };

// Request interception
axios.interceptors.request.use((config) => {
  const _token = token.get();
  if (_token && config.url) {
    config.headers.Authorization = `Jwt ${_token}`;
  }
  let url = config.url ?? '';
  url = url.startsWith('/') ? url : `/${url}`;
  if (!url.includes('//')) {
    let baseUrl = config.baseURL ?? '';
    baseUrl = baseUrl.endsWith('/') ? baseUrl.substr(0, baseUrl.length - 1) : baseUrl;
    url = `${baseUrl}${url}`;
  }

  return config;
}, (error) => Promise.reject(error));

// Response Interception
axios.interceptors.response.use(
  (response) => {
    if (!response.headers['content-type'].includes('json')) {
      return response;
    }
    const data = response.data ?? defaultError;
    // return pipeAxios({ ...data, hash: response.headers.track_code });
    return { ...data };
  },
  (err) => {
    let response;
    if (err.response) {
      ({ response } = err);
    } else if (err.message.includes('timeout')) {
      response = { status: 408, data: { code: 408, message: err.message }, headers: {} };
    }
    response = response ?? { status: 426, data: { code: 426, message: err.message }, headers: {} };
    const data = typeof response.data === 'string' ? {
      message: response.data,
      code: response.status,
    } : response.data;
    return { ...data };
    // return pipeAxios({ ...data, hash: response.headers.track_code });
  },
);

export default axios;
