import { createRouter, createWebHashHistory } from 'vue-router';
import tokenUtils from '@/utils/token';
import { updateContext } from '@/api/auth';
import ProductView from '../views/ProductView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: 'category',
    meta: {
      title: 'Home',
    },
  },
  {
    path: '/category',
    name: 'category',
    component: () => import(/* webpackChunkName: "category" */ '../views/CategoryLayout.vue'),
    redirect: '/category/new-arrivals',
    children: [
      {
        path: 'new-arrivals',
        meta: {
          title: 'New Arrivals',
        },
        component: () => import(/* webpackChunkName: "product" */ '../views/ProductView.vue'),
      },
    ],
  },
  // {
  //   path: '/login',
  //   name: 'login',
  //   component: () => import(/* webpackChunkName: "about" */ '../views/LoginView.vue'),
  // },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

let cacheToken;

router.beforeEach(async (to, from) => {
  const storageToken = tokenUtils.get();

  if (!storageToken || storageToken !== cacheToken) {
    await updateContext();
    cacheToken = storageToken;
  }
});

router.onError((err) => {
  console.error(err);
});

export default router;
