/* eslint-disable import/prefer-default-export */
import testData from '@/test/product.json';
import axios from './axios';

/**
 * @link https://www.dropshipzone.com.au/apidoc/index.html#api-Product-GetProducts
 */
// eslint-disable-next-line arrow-body-style
export const fetchProductList = async (data) => {
  const res = await axios({
    method: 'get',
    url: '/products',
    params: data,
  });
  return res;
  // return testData;
};
